// takes a type of the form [] | [A] and a mapper from A -> B and returns a B or undefined
export function optional(candid, mapper) {
    if (candid === []) {
        return undefined;
    }
    return candid[0] !== undefined ? mapper(candid[0]) : undefined;
}
export function identity(x) {
    return x;
}
