import { UnsupportedValueError } from "../../utils/error";
import { message } from "../common/chatMappers";
export function muteNotificationsResponse(candid) {
    if ("Success" in candid) {
        return "success";
    }
    if ("ChatNotFound" in candid) {
        return "chat_not_found";
    }
    throw new UnsupportedValueError(`Unexpected ApiToggleMuteNotificationsResponse type received`, candid);
}
export function subscriptionExistsResponse(candid) {
    if ("Yes" in candid) {
        return true;
    }
    if ("No" in candid) {
        return false;
    }
    throw new UnsupportedValueError(`Unexpected ApiSubscriptionExistsResponse type received`, candid);
}
export function notification(candid) {
    if ("GroupMessageNotification" in candid) {
        return groupNotification(candid.GroupMessageNotification);
    }
    if ("DirectMessageNotification" in candid) {
        return directNotification(candid.DirectMessageNotification);
    }
    throw new Error(`Unexpected ApiNotification type received, ${candid}`);
}
export function groupNotification(candid) {
    return {
        kind: "group_notification",
        sender: candid.sender.toString(),
        message: message(candid.message),
        senderName: candid.sender_name,
        chatId: candid.chat_id.toString(),
        groupName: candid.group_name,
    };
}
export function directNotification(candid) {
    return {
        kind: "direct_notification",
        sender: candid.sender.toString(),
        message: message(candid.message),
        senderName: candid.sender_name,
    };
}
